.header {
	position: fixed;
	width: 100%;
	top: 0;
	left: 0;
	z-index: 5;
	height: 113px;
	background-color: rgba(255, 255, 255, 0.95);
	border-bottom: 1px solid rgba(0, 0, 0, 0.15); }

.header__wrap {
	padding-right: 534px;
	padding-left: 291px; }

.logo {
	float: left; }

.nav-menu {
	float: right;
	ul {
		width: 100%;
		padding-top: 49px;
		padding-bottom: 45px;
		li {
			margin: 0 20px;
			vertical-align: middle;
			display: inline-block;
			a {
				padding-bottom: 11px;
				color: #7248b1;
				font-family: 'Circe', sans-serif;
				font-size: 12px;
				text-transform: uppercase;
				font-weight: 400;
				transition: all .3s ease-out;
				&:hover {
					font-weight: 700;
					border-bottom: 4px solid #ff73ab; } } } } }

.intro {
	width: 100%;
	background-image: url("../img/bg-intro.jpg");
	background-size: cover;
	position: relative; }

.intro__wrap {
	padding: 320px 307px 185px 0; }

.intro__title {
	color: #262a62;
	font-family: 'CoreSansGRounded', sans-serif;
	font-size: 86px;
	font-weight: 700;
	line-height: 109px;
	text-align: right; }

.intro__steps {
	width: 430px;
	margin-top: 52px;
	margin-right: 55px;
	float: right;
	text-align: center;
	span {
		display: inline-block;
		width: 100%;
		margin: 5px 0;
		color: #252a62;
		font-family: 'Lintel', sans-serif;
		font-size: 26px;
		font-weight: 400;
		line-height: 38px; } }

.plus-dots::before,
.plus-dots::after {
	content: '';
	display: block;
	margin: 10px auto;
	width: 13px;
	height: 13px;
	border-radius: 13px;
	background-color: #111c4e; }

.arrow-down {
	border: solid #ac9455;
	border-width: 0 2px 2px 0;
	display: inline-block;
	padding: 5px;
	transform: rotate(45deg); }



.overlay {
	background-color: rgba(0, 0, 0, 0.7);
	bottom: 0;
	cursor: default;
	left: 0;
	opacity: 0;
	position: fixed;
	right: 0;
	top: 0;
	visibility: hidden;
	z-index: 99999;
	transition: opacity .5s; }

.popup {
	background-color: #fff;
	display: inline-block;
	top: 50%;
	left: 50%;
	opacity: 0;
	width: 870px;
	height: 930px;
	position: fixed;
	visibility: hidden;
	z-index: 999999;
	transform: translate(-50%, -50%);
	transition: opacity .5s, top .5s;
	border-radius: 3px;
	ul {
		padding: 52px 85px 49px 59px;
		overflow-y: scroll;
		max-height: 877px;
		li {
			margin-bottom: 25px; } }

	.close_window {
		position: absolute;
		padding: 5px;
		top: 10px;
		right: 20px;
		cursor: pointer;
		color: #252a62;
		font-size: 20px;
		font-family: 'CoreSansGRounded', sans-serif; } }

.popup__header {
	width: 100%;
	padding: 18px 0 14px 61px;
	background-color: #f38bb8;
	color: #252a62;
	font-family: 'CoreSansGRounded', sans-serif;
	font-size: 20px;
	font-weight: 700;
	text-transform: uppercase; }

.popup__question {
	display: block;
	margin-bottom: 20px;
	color: #252a62;
	font-family: 'CoreSansGRounded', sans-serif;
	font-size: 28px;
	font-weight: 700;
	line-height: 30px; }

.popup__answer {
	display: inline-block;
	padding-top: 20px;
	color: #898989;
	font-family: 'OpenSans', sans-serif;
	font-size: 14px;
	font-weight: 400;
	line-height: 22px; }

.about {
	margin-top: 66px;
