.countdown {
	margin-top: 42px;
	padding-bottom: 185px; }

.countdown__wrap {
	margin: 27px auto;
	padding: 7px;
	background-color: #0e1012;
	border-radius: 7px;
	box-shadow: 0 25px 55px rgba(0, 0, 0, 0.75); }

.countdown__message {
	color: #ff73ab;
	font-family: 'CoreSansGRounded', sans-serif;
	font-size: 40px;
	font-weight: 700;
	line-height: 40px;
	text-align: center; }

.flip-clock-wrapper {
	margin: 0;
	ul {
		margin: 0 1px;
		padding: 59px;
		line-height: 0;
		font-weight: normal; } }

.flip-clock-wrapper {
	ul {
		li {
			a {
				div {
					div {
						&.inn {
							color: #e4e4e4;
							font-family: 'Montserrat', sans-serif;
							font-size: 100px;
							line-height: 112px;
							background-color: #ff73ab;
							border-radius: 4px;
							text-shadow: none; } } } } } } }

.flip-clock-wrapper {
	ul {
		li {
			a {
				div {
					&.up {
						&:after {
							top: 56px; } } } } } } }

.flip-clock-dot {
	width: 4px;
	height: 4px;
	background: #e4e4e4;
	&.top {
		top: 45px;
		left: 11px; }
	&.bottom {
		bottom: 29px;
		left: 11px; } }

.flip-clock-divider {
	width: 26px;
	.flip-clock-label {
		top: 11em;
		right: -150px;
		color: #262b62;
		font-family: 'CoreSansGRounded', sans-serif;
		font-size: 14px;
		font-weight: 700;
		line-height: 19px;
		text-transform: uppercase;
		letter-spacing: 3px; }
	&.minutes {
		.flip-clock-label {
			right: -150px; } } }

